
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { AjaxHttp, AjaxImageHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isNullValue, getImageUrl, callPhone, copyObject, compressImage, createBlob } from "../../../js/utility.js"
import ListCell from '../../crm/components/ListCell.vue'
import $ from 'jquery'
import event from '../../../js/Event.js'
import { mapState } from 'vuex'

export default {
name: 'user',
components: {
Group,
Cell,
ListCell,
XInput,
Selector,
XTextarea,
},
data() {
return {
userInfo: {},
copyUserInfo: {},
is_edit: false,
previewerlist: [],
}
},
computed: {
...mapState({
userMInfo: state => state.userInfo
}),
show() {
return JSON.stringify(this.userInfo) !== "{}";
}
},
mounted() {
this.getInfo()
},
beforeRouteLeave(to, from, next) {
this.updateEditInfo();
next();
},
methods: {
getInfo() {
AjaxHttp('POST', 'm=user&a=view', { 'id': this.$route.params.id }, this, function(res, wself, success) {
if (success) {
if (res.is_edit === 1) {
wself.is_edit = true;
} else {
wself.is_edit = false;
}
res.data.thumb_path = getImageUrl(res.data.thumb_path);

wself.previewerlist = [{
src: getImageUrl(res.data.img),
}];

if (res.data.sex === '0') {
res.data.sex = '';
}
wself.userInfo = res.data;
wself.copyUserInfo = copyObject(wself.userInfo);
}
});
},
updateEditInfo() {
if (this.is_edit) {
if (this.copyUserInfo.sex !== this.userInfo.sex || this.copyUserInfo.telephone !== this.userInfo.telephone || this.copyUserInfo.email !== this.userInfo.email || this.copyUserInfo.address !== this.userInfo.address) {
AjaxSilentHttp('POST', 'm=user&a=edit', { 'id': this.$route.params.id, 'sex': this.userInfo.sex, 'telephone': this.userInfo.telephone, 'email': this.userInfo.email, 'address': this.userInfo.address }, this, function(res, wself, success) {
if (success) {}
});
}
}
},
callPhone() {
if (!isNullValue(this.userInfo.telephone)) {
callPhone(this.userInfo.telephone);
}
},
previewerUserImage() {
event.$emit('previewerAction', { list: this.previewerlist, index: 0 });
},
editUserImage() {
if ($(fileElem)) {
$(fileElem).click();
}
},
uploadimg(e) {
var src, url = window.URL || window.webkitURL || window.mozURL,
files = e.target.files;
for (var i = 0, len = files.length; i < len; ++i) {
var file = files[i];






var self = this;

compressImage(file, 0.3, (code) => {
var formdata = new FormData();
formdata.append('img', createBlob(code), file.name);
formdata.append('id', self.$route.params.id);
AjaxImageHttp('POST', 'm=user&a=edit', formdata, self, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.userMInfo.img = getImageUrl(res.data);
wself.userInfo.thumb_path = getImageUrl(res.data);
wself.$store.dispatch('setUserInfo', wself.userMInfo);
}
});
});
}
}
}
}




